import React, { Component } from "react";

import "./user_annotation.css";

class UserAnnotation extends Component {
  constructor() {
    super();
    this.state = {
      counter: 0,
      selectedCategory: "",
      categories: [
        {
          categoryName: "People",
          value: "people"
        },
        {
          categoryName: "Emotions",
          value: "emotions"
        }
      ],
      images : [
        {
            src: "https://c2.staticflickr.com/9/8817/28973449265_07e3aa5d2e_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c2.staticflickr.com/9/8356/28897120681_3b2c0f43e0_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c2.staticflickr.com/9/8817/28973449265_07e3aa5d2e_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c2.staticflickr.com/9/8356/28897120681_3b2c0f43e0_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
            class: "notSelected",
            selected : false
          },
          {
            src: "https://c4.staticflickr.com/9/8887/28897124891_98c4fdd82b_b.jpg",
            class: "notSelected",
            selected : false
          }
      ]
    };
    this.state.selectedCategory = this.state.categories[0];
  }

  render() {
    return (
      <div>
        <div className="row categories-row py-3">
          <div className="col">{this.renderCategoriesList()}</div>
        </div>

        <div className="row py-3">
          <div className="col">
            <div className="row">
              <div className="col d-flex justify-content-center">         
                <h3>
                  Click all images with -{" "}
                  {this.state.selectedCategory.categoryName}
                </h3>
                
              </div>
            </div>
            <div className="row">
              <div className="col d-flex justify-content-center">
                <h5>If there is none, click submit</h5>
              </div>
            </div>
          </div>
        </div>

        <div className="row">
            {this.renderImages()}
        </div>

        <div className="row mt-3 mb-5">
            <div className="col d-flex flex-row-reverse">
                <button className="btn btn-secondary btn-md px-5" onClick={()=>{this.submitImages()}}>Submit</button>
            </div>
        </div>
      </div>
    );
  }

  // select category by clicking on it
  selectCategory(category) {
    this.setState({
      selectedCategory: category
    });
  }

    // render categories from list on front-end
    renderCategoriesList() {
        let ref = this;
        return (
        <span>
            {this.state.categories.map(function(d, idx) {
            return (
                <span
                onClick={() => ref.selectCategory(d)}
                className="mx-2 curson-type-pointer"
                key={idx}
                >
                {d.categoryName}
                </span>
            );
            })}
        </span>
        );
    }

    // render image list on front-end
    renderImages () {
        let ref = this;
        return (
            <span className="col">
                <div className="row">
                    {this.state.images.map(function(image, idx) {
                      if (idx%5 === 0){
                        ref.state.counter +=5
                        return (
                          <div key={idx} className="col-lg-5 col-md-5 col-sm-12 image-container" onClick={() => ref.imageSelected(image,idx)}>
                              <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                          </div>
                        );
                      }
                      else if (idx%4 === 0){
                        ref.state.counter +=4
                        return (
                          <div key={idx} className="col-lg-4 col-md-4 col-sm-12 image-container" onClick={() => ref.imageSelected(image,idx)}>
                              <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                          </div>
                        );
                      }
                      else if (idx%3 === 0){
                        ref.state.counter +=3
                        return (
                          <div key={idx} className="col-lg-3 col-md-3 col-sm-12 image-container" onClick={() => ref.imageSelected(image,idx)}>
                              <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                          </div>
                        );
                      }
                      else {
                        ref.state.counter +=2
                        return (
                          <div key={idx} className="col-lg-2 col-md-2 col-sm-12 image-container" onClick={() => ref.imageSelected(image,idx)}>
                              <img src={image.src} className={'curson-type-pointer image ' + image.class}/>
                          </div>
                        );
                      }
                    })}
                </div> 
            </span>
        )
    }

    // image selected function
    imageSelected (image,id) {
        let imageArr = this.state.images;
        if (imageArr[id].selected){
            imageArr[id].class="notSelected"
            imageArr[id].selected = false
        }
        else {
            imageArr[id].class="selected"
            imageArr[id].selected = true
        }
        this.setState({
            images: imageArr
        })
    }

    // submit Images after tagging
    submitImages() {
        this.state.images.map(function(image, idx) {
            if (image.selected)
                console.log(image)
        })
    }
}

export default UserAnnotation;
